.single-char {
    margin-top: 50px;
    display: flex;

    .char-img {
        width: 293px;
        height: 293px;
        margin-right: 50px;
    }
    .char-info {
        .char-title {
            font-weight: 700;
            font-size: 22px;
        }
        .char-descr {
            display: block;
            width: 550px;
            margin-top: 25px;
            font-size: 18px;
        }
    }
}